@font-face { font-family: Ogg;
    src: url(https://files.jcink.net/uploads2/resin/Ogg_Roman.otf); }
  


nav {
    width: 100%;
    height: 130px;

    z-index: 1;

    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5) 0%, rgba(65, 65, 65, 0.05) 100%), radial-gradient(at top center, rgba(255, 255, 255, 0.565) 0%, rgba(0, 0, 0, 0.08) 120%) #989898;;
    display: fixed;
    box-shadow: 0 5px 20px 5px rgba(0, 0, 0, 0.448);
}



figure {
    position: absolute;
    top: 15px;
    left: 20px;

}

#callToAction {
    background-color: rgb(122, 122, 122);
    height: 50px;
    position: relative;
    z-index: 2;
    padding-top: 15px;

    span {
        color: yellow;
        font-weight: 700;
    }

    h2 {
        width: 100%;
        font-size: 1rem;
        text-align: center;
        color: white;
        font-weight: 300;

    }
}
    


@media (min-width: 768px) {
  /* ONLY FOR SMALL SCREEN */
    .dropDownMenu .contactButton { 
        display: none;
    }.hamburgerButton {
        display: none;
    }
    /************************/

    nav {
        height: 150px;

    }

    main {
        position: relative;


    }

    .contactButton {
        position: absolute;
        right: 0;
        top: 20px;
        margin-right: 5px;
    }


    .mainMenu {
        position: relative;
        top: 30px;
        margin: 0;
        display: flex;
        justify-content: center;
        align-content: center;

        list-style-type: none;

        a {
            text-decoration: none;
            padding: 10px;

            font-size: clamp(1rem, 2vw +  1rem, 1.8rem);
            font-family: ogg;
            color: black;
        }
    }

    


    .dropDownMenu {
        display: none;
    }

    .nestListItem {
        display: none;
    }  
}

@media (min-width: 490px) and (max-width: 768px) {
    /* ONLY FOR SMALL SCREEN */
    .dropDownMenu .contactButton { 
        display: none;
    }.hamburgerButton {
        display: none;
    }
    /************************/

    nav {
        height: 150px;

    }

    main {
        position: relative;


    }

    .contactButton {
        position: absolute;
        right: 0;
        margin-right: 5px;
    }


    .mainMenu {
        position: relative;
        top: 30px;
        margin: 0;
        display: flex;
        justify-content: center;
        align-content: center;

        list-style-type: none;

        a {
            text-decoration: none;
            padding: 10px;

            font-size: 20px;
            color: black;
        }
    }

    


    .dropDownMenu {
        display: none;
    }

    .nestListItem {
        display: none;
    }
}

@media (max-width: 490px) {

    nav {
        height: 130px;

    }
    nav main {
        display: flex;
        position: relative;
    }
    
    .mainMenu {
        display: none;
    }
    .contactButton {
        display: none;
    }




    .dropDownMenu .contactButton {
        display: block;
    }

    .dropDownMenu {

        position: fixed;
        top: -100%;
        width: 100%;
        height: auto;

        z-index: 2;

        padding-bottom: 40px;
        background-color: white;

        transition: top 500ms ease-in-out;
    }
    .sideDown {
        top: 0;
    }

    .dropDownMenu ul {
        list-style-type: none;
        width: 90%;

        padding: 0px 0px 0px 10px;

        li {
            border-bottom: 1.5px solid lightgray;
            width: 90%;

            padding: 10px 0px 25px 0px;

            a {
                font-size: 40px;
                text-decoration: none;
                color: black;
            }
        }
    }

    .nestListItem {
        height: 430px;
        overflow: hidden;

        transition: height 300ms ease-in-out;
    }

    .nestListItem ul {
        margin-top: 20px;

        
        li {
            border: 0;
            padding: 10px 0px;
            margin: 5px 0px;

            a {
                font-size: 20px;
                padding: 20px 0px;

            }
        }

    }
    .hideListItem {
        height: 0;
        overflow: hidden;
        margin: 0;
    }


    .contactButton{
    
        margin: 60px auto 40px auto;
    
        background-color: red;
        width: 150px;
        height: 50px;
    
        border-radius: 30px;
    
        a {
            position: relative;
            bottom: 3.5px;
            right: 3px;
    
            display: inline-flex;
            width: 100%;
            height: 100%;
            border-radius: 30px;
            background-color: #989898;
    
            justify-content: center;
            align-items: center;
    
            text-decoration: none;
    
            font-size: 20px;
        }
    }


    .hamburgerButton {
        position: absolute;
        right: 0;
        top: 25%;

        z-index: 3;

        border: 0;
        border-radius: 5px;
        cursor: pointer;
        display: inline-block;
        
        padding: 10px;
        margin-right: 10px;

        background-color: #ffffff00;

    }.bar1,
    .bar2,
    .bar3 {
        width: 35px;
        height: 4px;
        border-radius: 2px;
        background-color: black;
        margin: 6px;


        transition: 300ms;
    }.change .bar1 {
        transform: translate(0, 9px) rotate(-45deg);
    }
    .change .bar2 {
        opacity: 0;
    }
    .change .bar3 {
        transform: translate(0, -9px) rotate(45deg);
    }

}